body {
  font-family: 'Rubik', sans-serif;
  position: relative;
  font-weight: 400;
  font-size: 15px;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a:hover, a:focus {
  text-decoration: none;
  transition: 0.3s ease;
}

a:focus {
  outline: 0;
}

img {
  max-width: 100%;
}

input[type=date],
input[type=datetime-local],
input[type=email],
input[type=number],
