.product .product__thumbnail {
  position: relative;
}

.product .product__thumbnail img {
  border-radius: 4px 4px 0 0;
  width: 100%;
}

.product .product__thumbnail:before {
  position: absolute;
  content: "";
  transition: 0.3s;
  height: 100%;
  opacity: 0;
  z-index: 2;
  border-radius: 4px 4px 0 0;
  width: 100%;
  top: 0;
  background-image: linear-gradient(to left, #34bfa3 0%, #5867dd 100%);
  /* Fallback (could use .jpg/.png alternatively) */
  background-color: #34bfa3;
  /* SVG fallback for IE 9 (could be data URI, or could use filter) */
  background: #5867dd;
  /* Safari 4, Chrome 1-9, iOS 3.2-4.3, Android 2.1-3.0 */
  background-image: -webkit-gradient(linear, left top, right top, from(#34bfa3), to(#5867dd));
  /* Safari 5.1, iOS 5.0-6.1, Chrome 10-25, Android 4.0-4.3 */
  background-image: -webkit-linear-gradient(left, #34bfa3, #5867dd);
  /* Firefox 3.6 - 15 */
  background-image: -moz-linear-gradient(left, #34bfa3, #5867dd);
  /* Opera 11.1 - 12 */
  background-image: -o-linear-gradient(left, #34bfa3, #5867dd);
  /* Opera 15+, Chrome 25+, IE 10+, Firefox 16+, Safari 6.1+, iOS 7+, Android 4.4+ */
  background-image: linear-gradient(to right, #34bfa3, #5867dd);
  left: 0;
}

